<template>
  <div class="outer">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="title">ÂæÆÂçöÂ§öÁî®Êà∑ÊÉÖÊÑüÂàÜÊûêÁªìÊûú</span>
        <el-button style="float: right; padding: 3px 0" type="text"></el-button>
      </div>
      <div class="user">
        <div class="text item" v-for="(o, index) in groupInfos" :key="index" :title="infos(o.user[0].fields)" @click="goInWb(o.user[0].pk)">
          <div class="m_l">
            <img :src="o.user[0].fields.Image">
          </div>
          <div class="m_r">
          <p>{{o.user[0].fields.nick_name}}</p>
          <div>ÁÆÄ‰ªãÔºö
            <span v-if="o.user[0].fields.brief_introduction">
              {{o.user[0].fields.brief_introduction}}
            </span>
            <span v-else>
              Êó†
            </span>
          </div>
          </div>
        </div>
      </div>
      <div class="userinfo">
        <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName">
          <el-table-column
            prop="index"
            label="‰∏ªÈ°µ">
          </el-table-column>
          <el-table-column
            prop="place"
            label="ÊâÄÂú®Âú∞Êñπ">
          </el-table-column>
          <el-table-column
            prop="birthday"
            label="ÁîüÊó•/ÊòüÂ∫ß">
          </el-table-column>
          <el-table-column
            prop="sex"
            label="ÊÄßÂèñÂêë">
          </el-table-column>
          <el-table-column
            prop="emotions"
            label="ÊÉÖÊÑüÁä∂ÊÄÅ">
          </el-table-column>
          <el-table-column
            prop="vip"
            label="VIPÁ≠âÁ∫ß">
          </el-table-column>
          <el-table-column
            prop="auth"
            label="ËÆ§ËØÅ">
          </el-table-column>
        </el-table>
      </div>
      <div class="usermain">
        <p>ËØç‰∫ëÂàÜÊûê</p>
        <span v-if="chartData === null">Ê≠£Âú®ÁñØÁãÇËÆ°ÁÆó‰∏≠...</span>
        <div v-else>
          <ve-wordcloud :data="chartData" :settings="chartSettings1"></ve-wordcloud>
          <div class="well fz14" style="padding: 15px 2px;font-size: 13px;margin-bottom: 0;background-color:#f7f9fa !important">Áî®Êà∑
            <span v-for="(o, index) in groupInfos" :key="index">
              @<strong>{{o.user[0].fields.nick_name}}</strong>
            </span>
            ÁöÑÂæÆÂçöÁöÑËØÑËÆ∫‰∏≠ÔºåËØçÊ±á<strong id="analysis_time">{{this.chartData.rows[0].word}}</strong>È¢ëÁéáÊúÄÈ´òÔºåËææÂà∞<strong id="bloger">{{this.chartData.rows[0].count}}</strong> Ê¨°„ÄÇ
            ÂÖ∂Ê¨°ÊòØ<strong id="analysis_time">{{this.chartData.rows[1].word}}„ÄÅ{{this.chartData.rows[2].word}}„ÄÅ{{this.chartData.rows[3].word}}</strong>ÂàÜÂà´ËææÂà∞
            <strong id="bloger">{{this.chartData.rows[1].count}}„ÄÅ{{this.chartData.rows[2].count}}„ÄÅ{{this.chartData.rows[3].count}}</strong> Ê¨°„ÄÇ
          </div>
        </div>
        <p>ÊïèÊÑüÁéáÔºàÂΩìÂâçÊïèÊÑüÁéáÂè™Ê£ÄÊµãÊö¥ÊÅê„ÄÅÂèçÂä®„ÄÅÊ∞ëÁîü„ÄÅËâ≤ÊÉÖÁ≠âËØçÊ±áÔºâ</p>
        <span v-if="chartData1 === null">Ê≠£Âú®ÁñØÁãÇËÆ°ÁÆó‰∏≠...</span>
        <div v-else>
          <ve-bar :data="chartData1" height="3.4rem"></ve-bar>
          <div class="well fz14" style="padding: 15px 2px;font-size: 13px;margin-bottom: 0;background-color:#f7f9fa !important">Áî®Êà∑
            <span v-for="(o, index) in groupInfos" :key="index">
              @<strong>{{o.user[0].fields.nick_name}}</strong>
            </span>
            ÁöÑÊâÄÊúâÂæÆÂçöÂàÜÊûê‰∏≠ÔºåÊïèÊÑüÂç†ÊØî<strong>{{mingandata * 100}}%</strong>,
            Âú®<strong>
              <span v-if="mingandata < 0.25">ÊûÅ‰Ωé</span>
              <span v-else-if="mingandata >= 0.25 && mingandata < 0.5">‰Ωé</span>
              <span v-else-if="mingandata >= 0.5  && mingandata < 0.75">È´ò</span>
              <span v-else>ÊûÅÈ´ò</span>
            </strong>ÊïèÊÑüËåÉÂõ¥ÂÜÖ
          </div>
        </div>
        <p>ÊÉÖÊÑüÂàÜÊûêÊü±Áä∂Âõæ</p>
        <span v-if="chartData2 === null">Ê≠£Âú®ÁñØÁãÇËÆ°ÁÆó‰∏≠...</span>
        <div v-else>
          <ve-histogram :data="chartData2" :settings="chartSettings2"></ve-histogram>
          <div class="well fz14" style="padding: 15px 2px;font-size: 13px;margin-bottom: 0;background-color:#f7f9fa !important">Áî®Êà∑
            <span v-for="(o, index) in groupInfos" :key="index">
              @<strong>{{o.user[0].fields.nick_name}}</strong>
            </span>
            ÁöÑÂæÆÂçöËØÑËÆ∫‰∏≠Ê∂àÊûÅËØÑËÆ∫ÊúÄÂ∞èÂÄº<strong>{{ this.emtionanaly.smalldate }} </strong>
            ÔºåÊ¨°Êï∞ÊòØ<strong>{{ this.emtionanaly.smallcount }} </strong>Ôºõ
            ÁßØÊûÅËØÑËÆ∫ÊúÄÂ§ßÂÄº<strong>{{ this.emtionanaly.bigdate }} </strong>
            ÔºåÊ¨°Êï∞ÊòØ<strong>{{ this.emtionanaly.bigcount }} </strong>Ôºõ
            ÊúÄÂ§ßÁöÑËØÑËÆ∫Ê¨°Êï∞ÊòØ<strong>{{ this.emtionanaly.maxcount }} </strong>Ê¨°
            ÔºåÊÉÖÊÑüÂÄºÊòØ<strong>{{ this.emtionanaly.maxdate }} </strong>„ÄÇ
            ÂÖ®ÈÉ®ËØÑËÆ∫‰∏≠ÔºöÊ∂àÊûÅËØÑËÆ∫ÂÜÖÂÆπÂç†ÊØî<strong>{{ (this.emtionanaly.count0 / this.emtionanaly.len)*100 }}%</strong>Ôºå
            ÁßØÊûÅËØÑËÆ∫ÂÜÖÂÆπÂç†ÊØî<strong>{{ (this.emtionanaly.count1 / this.emtionanaly.len)*100 }}%</strong>„ÄÇ
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
import Qs from 'qs'

import {
  mapState
} from 'vuex'
export default {
  name: 'GroupMain',
  data () {
    this.chartSettings1 = {
      sizeMin: 30,
      sizeMax: 60
    }
    this.chartSettings2 = {
      metrics: ['Ê¨°Êï∞'],
      dimension: ['ÊÉÖÊÑüÂÄº']
    }
    return {
      tableData: [],
      chartData: null,
      chartData1: null,
      chartData2: null,
      mingandata: 0,
      loading: '',
      emtionanaly: {
        count0: 0,
        count1: 0,
        len: 1,
        maxdate: 0,
        maxcount: 0,
        smalldate: 0,
        smallcount: 0,
        bigdate: 0,
        bigcount: 0
      }
    }
  },
  computed: {
    ...mapState({
      groupInfo: state => state.group
    }),
    groupInfos: function () {
      let res = this.groupInfo
      for (let i = 0; i < res.length; i++) {
        res[i].user = eval('(' + res[i].user + ')')
      }
      return res
    }
  },
  methods: {
    infos: function (infos) {
      return 'ÂæÆÂçöÊï∞Ôºö' + infos.tweets_num + '  Á≤â‰∏ùÊï∞Ôºö' + infos.fans_num + '  ÂÖ≥Ê≥®Êï∞Ôºö' + infos.follows_num
    },
    tableRowClassName ({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    setTableData: function () {
      let res = []
      for (let i = 0; i < this.groupInfos.length; i++) {
        res.push({
          'index': this.groupInfos[i].user[0].fields.nick_name,
          'place': this.groupInfos[i].user[0].fields.city ? this.groupInfos[i].user[0].fields.city : 'Êó†',
          'birthday': this.groupInfos[i].user[0].fields.birthday ? this.groupInfos[i].user[0].fields.birthday : this.groupInfos[i].user[0].fields.constellation,
          'sex': this.groupInfos[i].user[0].fields.sex_orientation ? this.groupInfos[i].user[0].fields.sex_orientation : 'Êó†',
          'emotions': this.groupInfos[i].user[0].fields.sex_orientation ? this.groupInfos[i].user[0].fields.sex_orientation : 'Êó†',
          'vip': this.groupInfos[i].user[0].fields.vip_level ? this.groupInfos[i].user[0].fields.vip_level : 'Êó†',
          'auth': this.groupInfos[i].user[0].fields.authentication ? this.groupInfos[i].user[0].fields.authentication : 'Êó†'
        })
      }
      this.tableData = res
    },
    goInWb: function (id) {
      this.openFullScreen2()
      axios.post('http://localhost:8000/spiderapi/',
        Qs.stringify({
          weiboId: id
        })
      ).then((response) => {
        this.$store.state.user = response.data.data
        this.$store.state.usertweets = response.data.tweets
        this.$store.state.total = response.data.total
        this.loading.close()
        this.$router.push({
          path: '/user'
        })
      })
    },
    openFullScreen2 () {
      this.loading = this.$loading({
        lock: true,
        text: 'ÂêéÂè∞ÁñØÁãÇËøõË°åÁà¨Ëô´ËÆ°ÁÆó‰∏≠',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
    }
  },
  mounted () {
    this.setTableData()
    this.$notify.info({
      title: 'Ê∂àÊÅØ',
      message: 'ËØ∑Á®çÂêéÔºåÊ≠£Âú®ËÆ°ÁÆóÊúÄÁªàÊÉÖÊÑüÁªìÊûú',
      position: 'bottom-right'
    })
    axios.get('http://localhost:8000/getgroup?&weiboIds=' + this.$store.state.tempids)
      .then((response) => {
        let res = []
        console.log(response.data)
        for (let i = 0; i < response.data.cipin.length; i++) {
          res.push({
            'word': response.data.cipin[i].word,
            'count': response.data.cipin[i].count
          })
        }
        let chartData = {
          columns: ['word', 'count'],
          rows: res
        }
        this.chartData = chartData
        let chartData1 = {
          columns: ['ÊïèÊÑüÁ®ãÂ∫¶', 'ÊïèÊÑü', 'ÈùûÊïèÊÑü'],
          rows: [
            { 'ÊïèÊÑüÁ®ãÂ∫¶': 'ÊïèÊÑüÁ®ãÂ∫¶', 'ÊïèÊÑü': parseFloat(response.data.mingan).toFixed(2), 'ÈùûÊïèÊÑü': 1 - parseFloat(response.data.mingan).toFixed(2) }
          ]
        }
        this.mingandata = parseFloat(response.data.mingan).toFixed(2)
        this.chartData1 = chartData1
        let rows = []
        this.emtionanaly.len = response.data.analy.length
        this.emtionanaly.smalldate = response.data.analy[0][0]
        this.emtionanaly.smallcount = response.data.analy[0][1]
        this.emtionanaly.bigdate = response.data.analy[response.data.analy.length - 1][0]
        this.emtionanaly.bigcount = response.data.analy[response.data.analy.length - 1][1]
        for (let i = 0; i < response.data.analy.length; i++) {
          if (response.data.analy[i][1] > this.emtionanaly.maxcount) {
            this.emtionanaly.maxcount = response.data.analy[i][1]
            this.emtionanaly.maxdate = response.data.analy[i][0]
          }
          rows.push({
            'ÊÉÖÊÑüÂÄº': response.data.analy[i][0],
            'Ê¨°Êï∞': response.data.analy[i][1]
          })
          if (response.data.analy[i][0] < 0.5) {
            this.emtionanaly.count0++
          } else {
            this.emtionanaly.count1++
          }
        }
        let chartData2 = {
          columns: ['ÊÉÖÊÑüÂÄº'],
          rows: rows
        }
        this.chartData2 = chartData2
        let pl = 'Ê∂àÊûÅÂÅèÂ§öüò≠'
        if (this.emtionanaly.count0 > this.emtionanaly.count1) {
          pl = 'Ê∂àÊûÅÂÅèÂ§öüò≠'
        } else {
          pl = 'ÁßØÊûÅÂÅèÂ§öüòÅ'
        }
        this.$notify({
          title: 'ÊàêÂäü',
          message: 'ÂæÆÂçöÂÜÖÂÆπËØÑËÆ∫ÂÄæÂêëÔºö' + pl,
          type: 'success',
          position: 'bottom-right'
        })
      })
  }
}

</script>

<style lang="scss" scoped>
  .outer {
    margin: 0 auto;
    max-width: 900px;
  }
  .title {
    font-size: 16px;
    padding: 15px 10px 15px 50px;
    margin: 0 0 10px;
    color: #FFF;
    text-align: center;
    background-color: #F8661E;
    background-image: url('../../../assets/icon-wyq.png');
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 50px;
    border-radius: 3px;
  }

  .item{
    padding: 10px;
    overflow: hidden;
    width: 280px;
    display: inline-block;
  }

  .item:hover {
    background-color: #E4E7ED;
    cursor: pointer;
  }

  .item .m_l {
    width: 50px;
    float: left;
    img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      display: block;
      cursor: pointer;
      padding: 10px 0;
    }
  }

  .m_r {
    margin-left: 80px;
    font-size: 14px;
    p {
      color: #0000EE;
    }
  }

  .user, .userInfo {
    border-bottom: 1px solid #E4E7ED;
  }

  /deep/ .el-table .warning-row {
    background: oldlace;
  }

  /deep/ .el-table .success-row {
    background: #f0f9eb;
  }

  .usermain {
    P {
      color: #1d77b4;
      font-size: 0.28125rem;
      margin: 0.234375rem 0;
      border-bottom: 1px solid #E4E7ED;
    }
  }

</style>



